import React from "react";
import PropTypes, { shape } from "prop-types";
import defaultPicture from "../../default-avatar.webp";
import friendList from "./friendList.module.css";


const FriendList = ({friends}) => (
    
    <ul className="friend-list">
        
    {friends.map(friend => (
        <li className={friendList.item} key={friend.id}>
            <span className={friendList.status} style={{
                    backgroundColor: `${friend.isOnline ? 'green' : 'tomato'}`,
            }}>{friend.isOnline }</span>
            <img className={friendList.avatar} src={friend.avatar} alt={friend.name} />
            <p className={friendList.name}>{friend.name }</p>
        </li>
        ))} 
      
    </ul>
);

FriendList.defaultProps = {
    avatar: defaultPicture,
};

FriendList.propTypes = {
    friends: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.number.isRequired,
    avatar: PropTypes.string,
    name: PropTypes.string.isRequired,
    isOnline: PropTypes.bool, 
    }  
    ))
    
};

export default FriendList;
