import React from "react";
import PropTypes from "prop-types";
import defaultPicture from "../../default-avatar.webp";
import styles from "./profile.module.css";

const Profile = ({ avatar, name, tag, location, stats}) => (
  <div className={styles.profile}>
    <div className={styles.description}
      >
      <img className={styles.description__img}
        src={avatar}
        alt="Аватар пользователя"
        
      />
      <p className={styles.description__name}>{name}</p>
      <p className="description__tag">@{tag}</p>
      <p className="description__location">Location: {location}</p>
    </div>

    <ul className={styles.stats}>
      <li>
        <span className="label">Followers: </span>
        <span className={styles.quantity}>{stats.followers}</span>
      </li>
      <li>
        <span className="label">Views: </span>
        <span className={styles.quantity}>{stats.views}</span>
      </li>
      <li>
        <span className="label">Likes: </span>
        <span className={styles.quantity}>{stats.likes}</span>
      </li>
    </ul>
  </div>
);
    
Profile.defaultProps = {
  avatar: defaultPicture
};
Profile.propTypes = {
  avatar: PropTypes.string,
  name: PropTypes.string.isRequired,
  tag: PropTypes.string.isRequired,
  location: PropTypes.string.isRequired,
  stats: PropTypes.objectOf(PropTypes.number),
};

export default Profile;